export default {
	data() {
		return {
			userInfo: {
				phone: '',
				nickname: '',
				avatar:"",
			},
		};
	},
	onLoad() {
		this.getUserInfo();
	},
	methods: {
		//更换用户头像
		changeHead() {
			let _self = this;
			uni.chooseImage({
				success: function(e) {
					var imagePathArr = e.tempFilePaths;
					uni.uploadFile({ 
						url: _self.url+'/ebapi/public_api/upload',
						fileType: 'image',
						formData: {
							filename: 'keyname'
						},
						filePath: imagePathArr[0],
						name: 'keyname',
						header: {
							'Content-Type': 'multipart/form-data'
						},
						success: function(res) {
							var data = JSON.parse(res.data);
							console.log(data);
							_self.userInfo.avatar= data.data.url;	//服务器
							// _self.userInfo.avatar= _self.url+data.data.url;	//本地
							uni.showToast({
								title: '上传进度：' + 1 + '/' + imagePathArr.length,
								icon: 'none',
								mask: false,
								duration: 1000
							});
						}
					});
				}
			});
		},
		saveUserInfo(){
			let that = this;
			that.basePost(
				that.U({ c: 'user_api', a: 'edit_user' }),
				{
					nickname: that.userInfo.nickname,
					avatar:that.userInfo.avatar,
				},
				function(res) {
					that.Tips({title:'修改成功'},{tab:3 ,url :1});
				},
				function(res) {
					that.Tips({title:'修改失败'});
				}
			);
		},
		//获取用户的信息
		getUserInfo() {
			let that = this;
			that.basePost(
				that.U({ c: 'user_api', a: 'my' }),
				{},
				function(res) {
					that.userInfo = res.data;
				},
				function(res) {
					console.log(res);
				}
			);
		},
		nicknames(name) {
			uni.navigateTo({
				url: '/pages/login/modify_nicknames/modify_nicknames?name=' + name
			});
		}
	}
};
